export default {
	props:{
		headPicShow:{ //图片
			type:[String,Boolean],
			default:true,
		},
		headPicValue:{
			type:String,
			default:require('@/static/st_pic.png')
		},
		
		headTitleShow:{ //标题
			type:[String,Boolean],
			default:true,
		},
		headTitleValue:{
			type:String,
			default:"描述相符"
		},
		
		starsShow:{
			type:[String,Boolean],
			default:true,
		},
		starsMax:{ // 星星最大个数
			type:[String,Number],
			default:5,
		},
		starDefault:{ //未选中
			type:String,
			default:require('@/static/st_star.png'),
		},
		starActive:{
			type:String,
			default:require('@/static/st_star_active.png'),
		},
		score:{  //默认分数
			type:[Number,String],
			default:0
		},
		starsDisabled:{ //是否禁用star
			type:[Boolean],
			default:false
		},
		
		textareaShow:{ // 多行文本显示
			type:[String,Boolean],
			default:true,
		},
		textareaPlaceholder:{
			type:[String],
			default:"课程满足你的期待吗？说说你的使用心得，分享给想报名的家长们吧"
		},
		
		
		submitShow:{ // 发布按钮
			type:[String,Boolean],
			default:true,
		},
		submitText:{
			type:String,
			default:"发 布",
		},
		
		infoReceive:{ // 获取值
			type:Object,
			default:function(){
				return {
					score:0,
					textareaValue:""
				}
			}
		}
	},
	data() {
		return {
			textareaInfo: ''
		}
	},
	computed:{
		formatScore(){
			return this.infoReceive.score
		}
	},
	methods: {
		/**
		 * @name 设置分数
		 */
		setScore(score){
			if(this.starsDisabled!==false)return
			this.infoReceive.score=score
			this.$emit("scoreChange",score)
		},
		
		/**
		 * @name 获取textarea内容
		 */
		
		blur(e){
			this.infoReceive.textareaValue=e.detail.value
		},
		
		/**
		 * @name 提交
		 */
		doSubmit(){
			// this.$emit('submit',this.infoReceive)
			if(this.infoReceive.textareaValue) {
				uni.showLoading({
					title: '正在发布...',
					mask: true,
					success() {
						setTimeout(() => {
							uni.hideLoading()
							uni.showToast({
								icon: "success",
								title: '发布成功',
								duration: 1000
							});
							setTimeout(() => {
								uni.navigateBack({
									delta: 1  // 上一页
								})
							},1000)
						}, 1500)
					}
				})
			} else {
				uni.showToast({
					icon: "none",
					title: '评价内容信息不能为空',
					duration: 2000
				});
			}
		}
	},
	created() {
		this.infoReceive.score=this.score
	}
}
